/* Flex styles -------------------------------------------------*/
@media (min-width: 700px) {
 .leading {
    display: flex;
    align-items: center;
  }
}


/* Grid styles -------------------------------------------------*/
.cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 30px;
}


/* Generic styles ----------------------------------------------*/
body {
  font-family: 'Quicksand', sans-serif;
}

.logo {
  height: 50px;
  margin-right: 20px;
}

.cards {
  max-width: 70%;
  margin: 0 auto 30px;
}

article {
  position: relative;
}

.hero-img {
  height: 200px;
  width: 100%;
  object-fit: cover;
}

.main-menu{
    list-style:none;
    margin-bottom: 40px;
    padding:0;
    text-align:center;
}

.menu-item {
  text-decoration: none;
  background-color: #EEEEEE;
  color: #333333;
  padding: 2px 6px 2px 6px;
  border-top: 1px solid #CCCCCC;
  border-right: 1px solid #333333;
  border-bottom: 1px solid #333333;
  border-left: 1px solid #CCCCCC;
  margin-left: 30px;
  font-size: 2em;
}
